@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');

.ls-main-distributed{box-sizing: border-box;width: 100%;}
.ls-header-distributed{box-sizing: border-box;width: 100%;}
/** all header css **/
@media (max-width: 880px) {
    .ls-header ls-header-left,
    .ls-header ls-header-center-search,
    .ls-header ls-header-center-location,
    .ls-header ls-header-right{
		display: block;
		width: 100%;
		margin-bottom: 10px;
		text-align: center;
	}
	.ls-header .footer-center {
		margin-left: 0;
	}
}
.ls-header{display:-webkit-flex;display:flex;flex-direction: row; box-sizing: border-box;width: 100%;border-bottom:1px solid silver}
.ls-header ls-header-left,
.ls-header ls-header-center-search,
.ls-header ls-header-center-location,
.ls-header ls-header-right {display: inline-block;vertical-align: middle;}
.ls-header .ls-header-left img{width: 100%;}
.ls-header .ls-header-center-search{width: 40%;margin-top: 10px;}
.ls-header .ls-header-center-location{width: 40%;margin-top: 2px;}
.ls-header .ls-header-right{width: 10%;}
.ls-header .ls-header-left img{width: 100%;}
.ls-header .ls-header-right img{width: 100%;}
.ls-header-center-location-container{display:-webkit-flex;display:flex;flex-direction: column;overflow: auto;}
.ls-header-center-location-container ls-header-center-location-container-delivery {width:80%;flex: 1;}
.ls-header-center-location-container ls-header-center-location-container-supported {width:20%;flex: 1;}
.ls-header-center-location-container-supported .tooltiptext{visibility:hidden;width:150px;background-color:grey;color:#fff;text-align:center;padding:5px 0;border-radius:6px;position:absolute;z-index:1}
.responsive{height:auto;display:block}
.sloading{max-height: 40px;}
/* header ends */
.content-container{display:flex;width:100%}
.main-container{display:flex;flex-wrap:wrap;padding:5px}
.grid-container>div{background:#fff;border-radius:1rem}
.head-container>div{background:#fff}
.content-container>div{background:#fff;border-radius:1rem}
.nav-container{display:block;margin-top: 15px;}
.nav-container>div{background:orange;border-radius:1rem;margin-top: 15px;}
.ad-container{display:flex;flex-wrap:wrap;padding:5px;width:100%;height:50px}
.ad-container>div{background:#000;border-radius:1rem}
.side-container{display:flex;flex-wrap:wrap;padding:5px;flex:15%}
.side-container>div{background:#ff0;border-radius:1rem}
