.dropdown{
    background-color: cadetblue;
    flex-flow: row wrap;
    margin-top: 15px;
    width:100%;
}

.ls-menu-header {
    background: #33383b;
    padding: 16px;
    color: rgb(252, 249, 249);
  }

  div.ls-menu-header {
    text-align: center;
  }
  
  .column {
    float: left;
    width: 33.33%;
    padding: 10px;
    background-color: #ccc;
    height: 250px;
  }
  
  /* Style links inside the columns */
  .column a {
    float: none;
    color: black;
    padding: 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  /* Add a background color on hover */
  .column a:hover {
    background-color: #ddd;
  }

  /* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
  }