@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.search-container {
  font: 100% sans-serif;
  margin: 5px;
  border: 1px solid #c0c0c0;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

.field {  
  display: flex;
  position: relative;
}

.field--focused {
  border-color: #ccc;
}

#searchInput {
  border: 1px;
  box-sizing: border-box;
  margin: 0;
  padding: 25px;  
  font-size:12pt; 
  width:100%;
}

.clear-button,
.submit-button {
  background: transparent;
  background-position: center center;
  background-repeat: no-repeat;
  border: 0;
  cursor: pointer;
  font-size: 0;
  outline: 0;
  padding: 0;
  width: 25px;
}

.clear-button {
  background-image: url(/static/media/clear.58ec4791.svg);
  background-size: 30%;
  right: 0;
  width: 40px;
}

.submit-button {
  background-color: #e5e5e5;
  background-image: url(/static/media/search.5ba32183.svg);
  background-size: 35%;
  border: 1px solid #ddd;
  padding: 0 20px;
  opacity: .8;
  width: 40px;
}

.submit-button:hover {
  opacity: 1;
}

.suggestions {
  border: 1px solid #ccc;
  list-style: none;
  margin: 0;
  max-height: 240px;
  height:100%;
  width:40%;
  overflow: auto;
  padding: 0;
  position: fixed; 
  -webkit-user-select: none; 
      -ms-user-select: none; 
          user-select: none;
  cursor: pointer;
  z-index: 10;
  display: block;
  background-color: white;
}

.suggestion {
  cursor: pointer;
  height: 40px;
  letter-spacing: 0.03em;
  line-height: 40px;
  padding: 0 7px 0 10px;  
}

.suggestion:hover {
	background: #c0c0c0;
}

.suggestion--focused {
  background: #00ff00;
}

.suggestion:last-child {
  border: none;
}

.onfocus {
	background-color: orange;
}
.dropdown{
    background-color: cadetblue;
    flex-flow: row wrap;
    margin-top: 15px;
    width:100%;
}

.ls-menu-header {
    background: #33383b;
    padding: 16px;
    color: rgb(252, 249, 249);
  }

  div.ls-menu-header {
    text-align: center;
  }
  
  .column {
    float: left;
    width: 33.33%;
    padding: 10px;
    background-color: #ccc;
    height: 250px;
  }
  
  /* Style links inside the columns */
  .column a {
    float: none;
    color: black;
    padding: 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  /* Add a background color on hover */
  .column a:hover {
    background-color: #ddd;
  }

  /* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
  }

.modal-cover {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  transform: translateZ(0);
  background-color: rgba(0, 0, 0, 0.8);
}
.modal-area-new {
  width:100%;
  height: 100%;
}

.modal-area {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 2.5em 1.5em 1.5em 1.5em;
  background-color: #ffffff;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

@media screen and (min-width: 500px) {
  /* Center the Modal! */
  .modal-area {
    left: 50%;
    top: 50%;
    height: auto;
    transform: translate(-50%, -50%);
    max-width: 30em;
    max-height: calc(100% - 1em);
  }
}

._modal-close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.5em;
  line-height: 1;
  background: #f6f6f7;
  border: 0;
  box-shadow: 0;
  cursor: pointer;
}

._modal-close-icon {
  width: 25px;
  height: 25px;
  fill: transparent;
  stroke: black;
  stroke-linecap: round;
  stroke-width: 2;
}

.modal-body {
  padding-top: 0.25em;
}
._hide-visual {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.scroll-lock {
  overflow: hidden;
  margin-right: 17px;
}

.form-label {
	font-size: 15px;
	color: #008040;
	font-weight: bold;
}
.error {
    font-size: 10px;
	color: #ff0000;
	font-weight: bold;
}

.ls-main-distributed{box-sizing: border-box;width: 100%;}
.ls-header-distributed{box-sizing: border-box;width: 100%;}
